body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    margin: 0;
    background-color: #8ceff7;
    font-family: 'Courier New', Courier, monospace;
    height: 100%;
}

.container {
    max-width: 850px;
    height: 100%;
    margin: 0 auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f3f3f3;
    box-shadow: 3px 3px 5px;
    border-radius: 10px;
    padding: 50px;
}

.titulo {
    font-size: 35px;
    color: rgb(226, 74, 74);
    margin-bottom: 30px;
}

.subTitulo {
    font-size: 28px;
    color: rgb(74, 183, 226);
}

.pergunta {
    font-size: 25px;
    color: rgb(44, 152, 179);
    margin-bottom: 20px;
}

.opcoesContainer {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
}

.opcoes {
    width: 350px;
    background-color: rgb(223, 65, 65);
    color: #f1f1f1;
    border-radius: 5px;
    padding: 15px;
    margin: 10px 0;
    font-size: 18px;
}

.opcoes:hover {
    color: #000;
    padding: 20px;
    margin: 15px 5px;
    cursor: pointer;
    transition: 0.1s;
}

.resposta {
    font-size: 25px;
    color: rgb(44, 152, 179);
    margin-right: 15px;
    font-weight: bold;
}

.input {
    width: 30px;
    margin-right: 15px;
    border: 1px solid rgb(44, 152, 179);
    border-radius: 5px;
    text-align: center;
}

.button {
    background-color: #8ceff7;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: bold;
}

.button:hover {
    padding: 15px;
    color: #fff;
    cursor: pointer;
}

.pontos {
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.pontos p {
    font-size: 20px;
    color: rgb(44, 152, 179);
    margin-right: 15px;
    font-weight: bold;
}

.acerto {
    color: rgb(15, 172, 15) !important;
    font-weight: bold;
}

.erro {
    color: rgb(207, 24, 24) !important;
    font-weight: bold;
}

@media (max-width: 1200px) {
    .container {
        width: 600px;
        margin: 10px auto;
        padding: 35px;
    }

    .titulo {
        margin-bottom: 30px;
    }
}

@media (max-width: 725px) {
    .container {
        width: 450px;
        padding: 20px;
        margin: 10 auto;
    }

    .titulo {
        font-size: 30px;
    }

    .pergunta {
        font-size: 22px;
    }

}

@media (max-width: 525px) {
    .container {
        width: 280px;
        margin: 0 auto;
        padding: 30px;
    }
    

    .titulo {
        font-size: 18px;
    }

    .pergunta {
        font-size: 16px;
    }

    .opcoes {
        width: 250px;
    }
